<template>
  <div id="Software" class="container">
    <div class="row">
      <div id="left" class="col-md-3 col-xs-12">
        <ul class="left-container wow bounceInLeft">
          <p>产品中心</p>
          <li v-for="(item,index) in softwareList" :key="index">
            <el-popover placement="right" trigger="hover" v-if="item.children">
              <ul :data="item.children">
                <li
                  v-for="(item,index) in item.children"
                  :key="index"
                  class="left-container-hover-li"
                >
                  <el-popover placement="right" trigger="hover" v-if="item.children">
                    <ul :data="item.children">
                      <li
                        v-for="(item,index) in item.children"
                        :key="index"
                        class="left-container-hover-li"
                      >
                        <router-link :to="item.path">{{item.name}}</router-link>
                      </li>
                    </ul>
                    <router-link slot="reference" :to="item.path">{{item.name}}</router-link>
                  </el-popover>
                  <router-link :to="item.path" v-else>{{item.name}}</router-link>
                </li>
              </ul>
              <router-link slot="reference" :to="item.path">{{item.name}}</router-link>
            </el-popover>
            <router-link :to="item.path" v-else>{{item.name}}</router-link>
          </li>
        </ul>
      </div>
      <div id="right" class="col-md-9 col-xs-12 wow bounceInRight">
        <router-view></router-view>
      </div>
    </div>
  </div>
</template>
<script>
import { WOW } from "wowjs";
import { productMenu } from "../json/productMenu";

export default {
  name: "ProductCenter",
  data() {
    return {
      softwareList: productMenu
    };
  },
  mounted() {
    var wow = new WOW();
    wow.init();
  }
};
</script>
<style scoped>
#left {
  margin: 50px 0;
}
.left-container {
  width: 90%;
  margin: 0 auto;
  border: 1px solid #474747;
  border-radius: 5px;
}
.left-container > p {
  text-align: center;
  line-height: 45px;
  padding: 0;
  margin: 0;
  background: #474747;
  color: #fff;
  font-size: 18px;
  font-weight: bold;
}
.left-container > li {
  text-align: center;
  height: 38px;
  line-height: 38px;
  margin: 0;
  border-top: 1px solid #474747;
}
.left-container > li > a {
  text-decoration: none;
}
.left-container > li:hover {
  background: #928989;
}
.left-container-hover-li {
  text-align: center;
  height: 30px;
  line-height: 30px;
  margin: 0;
}
#right {
  padding: 50px 0;
}
@media screen and (max-width: 768px) {
  #right {
    padding: 15px;
  }
}
</style>




